<template>
  <my-layout>
    <el-form slot="form" :inline="true" size="mini" ref="searchForm" :model="searchForm" class="search_form">
      <el-form-item label="学生名" prop="courseName">
        <el-input placeholder="请输入学生名" clearable v-model="searchForm.studentName"
                  @keydown.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="课程名" class="search-item" prop="courseName">
        <el-input placeholder="请输入课程名" clearable v-model="searchForm.courseName"
                  @keydown.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="教师名" prop="teacherName">
        <el-input placeholder="请输入教师名" clearable v-model="searchForm.teacherName"
                  @keydown.enter.native="search"></el-input>
      </el-form-item>
      <el-form-item label="分数" prop="scoreCode">
        <el-select v-model="searchForm.scoreCode" placeholder="请选择">
          <el-option
              v-for="item in scoreOptions"
              :key="item.value"
              :label="item.name"
              :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item slot="default">
        <el-button type="primary" @click="search">查询</el-button>
        <el-button type="primary" @click="resetForm('searchForm')">重置</el-button>
      </el-form-item>
    </el-form>
    <template slot-scope="scope" slot="tableLayout">
      <my-table @getData="getTable" ref="table" :tableData="tableData" :height="scope.height" :totalCount="totalCount">
        <template slot="tableHead">
          <el-table-column type="index" :index="computeIndex" label="序号"></el-table-column>
          <el-table-column prop="studentName" label="学生名"></el-table-column>
          <el-table-column prop="courseName" label="课程名"></el-table-column>
          <el-table-column prop="teacherName" label="教师名"></el-table-column>
          <el-table-column
              prop="score"
              label="分数">
            <template slot-scope="scope">
              <span>{{scope.row.score===-1?'暂无':scope.row.score}}</span>
            </template>
          </el-table-column>
          <el-table-column width="150" fixed="right" label="操作">
            label="操作">
            <template slot-scope="scope">
              <el-button size="mini" type="primary" plain :data-id="scope.row.courseId"
                         @click="updateScore(scope.row)">修改
              </el-button>
            </template>
          </el-table-column>
        </template>
      </my-table>
    </template>
  </my-layout>
</template>
<script>
  export default {
    name: 'allCourses',
    data() {
      return {
        searchForm: {
          studentName: '',
          courseName: '',
          classroom: '',
          scoreCode: ''
        },
        scoreOptions: [
          {name: '请选择', value: ''},
          {name: '特优(90以上)', value: 1},
          {name: '优(80-89)', value: 2},
          {name: '良(70-79)', value: 3},
          {name: '及格(60-69)', value: 4},
          {name: '不及格(60以下)', value: 5}
        ],
        ruleFormIndex: 'ruleForm' + Math.ceil(Math.random() * 100),
        tableData: [],
        totalCount: 0,
        selectedCourseId: '',
        page: 1,
        count: 10,
        tableObj: {}
      }
    },
    mounted() {
      this.tableObj = this.$refs['table']
    },
    methods: {
      getTable(page, count) {
        this.page = page
        this.count = count
        this.getTableData.call(this, '/admins/allScores', this.searchForm)
      },
      updateScore(row) {
        this.$prompt('请输入分数', '提示', {
          inputValue: row.score > -1 ? row.score : '',
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          inputPattern: /^[0-9]+$/,
          inputErrorMessage: '格式不正确',
          inputValidator: function (v) {
            if (v < 0 || v > 150) {
              return '分数超过限制'
            }
          }
        }).then(value => {
          this.loading()
          this.axios.post('/admins/updateScore', {
            score: value.value,
            studentId: row.studentId,
            courseId: row.courseId
          }).then(r => {
              let data = r.data
              this.loaded && this.loaded.close()
              if (data.status === 0) {
                this.$message.success(data.message)
                this.tableObj.refresh()
              } else {
                this.$message.error(data.message)
              }
            }, err => this.$err(err)
          )
        });
      },
      computeIndex(index) {
        return (this.page - 1) * this.count + index + 1
      },
      // 重置
      resetForm(formName) {
        this.$refs[formName].resetFields()
      },
      search() {
        this.tableObj.search()
      }
    },
  }
</script>
